<template>
  <van-tabs v-model="active" color="#5A99F6" line-height="2px" animated class="carinfo" swipeable>
    <van-tab title="车型">
      <div v-if="chexing.length===0" class="wujiangjia">暂无车型</div>
      <div v-else>
        <p class="time">{{time}}款</p>
        <div v-for="(item, index) in chexing" :key="index" class="cheBox clearfix">
          <div class="name">
            <p class="carname">{{item.Car_Name}}</p>
            <p
              class="mali"
            >122马力{{item.UnderPan_ForwardGearNum!="无"?item.UnderPan_ForwardGearNum+"档":""}}{{item.UnderPan_TransmissionType}}</p>
          </div>
          <div class="price">
            <p class="min">{{item.MinPrice}}万起</p>
            <p class="max">指导价:{{item.MaxPrice}}万</p>
          </div>
          <div class="clearfix"></div>
          <div class="btn-group">
            <van-button type="info" plain>参数配置</van-button>
            <van-button type="info" plain>贷款</van-button>
            <van-button type="info" plain>询底价</van-button>
          </div>
        </div>
      </div>
    </van-tab>
    <van-tab title="降价">
      <div v-if="jiangjia.length===0" class="wujiangjia">该车型暂无降价信息</div>
      <div v-else>
        <div class="jiangjia" v-for="(item, index) in jiangjia" :key="index">
          <p class="carname">{{item.CarName}}</p>
          <div class="icon">
            <div>
              <p class="price">
                {{item.ActPrice}}万
                <del>{{item.ReferPrice}}万</del>
              </p>
              <p class="dealername">4S-{{item.DealerName}}</p>
            </div>
            <div class="righticon">
              <van-icon name="http://res.app.yiche.com/wechatapp/ic_zs_jiangjia@2x.png" />
              <span>{{item.FavPrice}}万</span>
            </div>
          </div>
          <div class="btn-group">
            <van-button type="info" plain @click="phone(item.CallCenterNumber)">400电话</van-button>
            <van-button type="info" plain>贷款</van-button>
            <van-button type="info" plain>询底价</van-button>
          </div>
        </div>
      </div>
    </van-tab>
    <van-tab title="资讯">
      <div v-if="zixun.length===0" class="wujiangjia">该车型暂无资讯</div>
      <div v-else>
        <div class="zixun" v-for="(item, index) in zixun" :key="index" @click="Todetail(item.newsid)">
          <van-image :src="item.PicCover" width="120" height="80" style="float: left;">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <div class="body">
            <p class="title">{{item.title}}</p>
            <p class="count">
              <span>{{item.publishtime}}</span>
              <span class="counts">
                <van-icon name="comment-o" />
                {{item.CommentCount}}
              </span>
            </p>
          </div>
        </div>
      </div>
    </van-tab>
    <van-tab title="经销商">
        <div v-if="distributor===undefined" class="wujiangjia">该车型暂无经销商</div>
        <div v-else>
            <div class="distributor" v-for="(item, index) in distributor" :key="index">
                <p class="dealer"><span>4S-</span>{{item.DealerName}}<span>{{item.minPrice}}万起</span></p>
                <p class="addr">{{item.DealerSaleAddr}}</p>
                <div class="btn-group">
            <van-button type="info" plain @click="phone(item.DealerTel)">400电话</van-button>
            <van-button type="info" plain>贷款</van-button>
            <van-button type="info" plain>询底价</van-button>
          </div>
            </div>
        </div>
    </van-tab>
  </van-tabs>
</template>
<script>
import * as api from "../../api/index.js";
export default {
  props: ["SerialID"],
  data() {
    return {
      active: 0,
      time: "", //上市时间
      chexing: [], //车型
      jiangjia: [], //降价
      zixun: [], //资讯
      distributor: [] //经销商
    };
  },
  methods: {
    phone(phone) {  //拨打电话弹框
      this.$dialog
        .confirm({ title: "拨打电话", message: "电话号码:" + phone })
        .then(() => {})
        .catch(() => {});
    },
	Todetail(id){
		this.$router.push({path:'/detail',query:{newsid:id}})
	},
    async getCarXing(id) {   //获取车型信息
      let data = await api.getCheXing(id);
      if (data.length > 0) {
        this.time = data[0].Car_YearType;
      }
      this.chexing = data;
    },
    async getJiangjia(id) {  //获取降价信息
      let data = await api.getJiangjia(id);
      this.jiangjia = data.Data;
    },
    async getZiXun(id) {    //获取资讯信息
      let data = await api.getZiXun(id);
      this.zixun = data.Data;
    },
    async getDistributor(id){  //获取经销商信息
        let data = await api.getJingXiao(id)
        this.distributor = data.Data.List
    }
  },
  watch: {
    SerialID(newVal) {
      this.getCarXing(newVal);
      this.getJiangjia(newVal);
      this.getZiXun(newVal);
      this.getDistributor(newVal);
    }
  },
  activated() {
    this.active = 0;
  }
};
</script>
<style lang="scss">
@import "../../style/carInfo.scss";
</style>