<template>
  <div class="carInfo">
    <!-- 标题 -->
    <van-nav-bar
      :title="title.AliasName"
      :fixed="true"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <!-- 图片 -->
    <van-image :src="titleImg" width="100%">
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
    </van-image>

    <!-- 价格 -->
    <div class="titlePrice">
      <div>
        <p class="price">{{title.DealerPrice}}</p>
        <p class="del">
          <del>{{title.ReferPrice==="暂无"?"":title.ReferPrice}}</del>
          |{{title.Level}}
        </p>
      </div>
      <div class="youhao">
        <van-image src="http://res.app.yiche.com/wechatapp/ic_zs_youhao@2x.png" width="20" />
        <br />油耗
      </div>
    </div>

    <!-- 贷款、询底价按钮 -->
    <div class="centerButton">
      <van-button color="#7232dd" plain class="daikuan">贷款</van-button>
      <van-button color="#7232dd" class="xundijia">询底价</van-button>
    </div>

    <div style="height:10px;background:#eee"></div>

    <!-- 选项卡 -->
    <cartab :SerialID="SerialID"></cartab>

    <!-- 底部内容 -->
    <div class="start">
      <div @click="Collection">
        <van-icon name="http://res.app.yiche.com/wechatapp/ic_cxzsy_sc@2x.png" />
        <p>收藏</p>
      </div>
      <div>
        <van-icon name="http://res.app.yiche.com/wechatapp/ic_cxzsy_fx@2x.png" />
        <p>分享</p>
      </div>
      <van-button type="info" color="#5F93FF">询底价</van-button>
    </div>
  </div>
</template>
<script>
import * as api from "../../api/index.js";
import cartab from "./carTab";
export default {
  data() {
    return {
      SerialID: "",
      title: "",
      picture:""
    };
  },
  computed: {
    titleImg() {
      if (this.title.CoverPhoto === "") {
        return "http://res.app.yiche.com/wechatapp/async750x420.png";
      } else {
        let data = this.title.CoverPhoto;
        let data2 = this.title.Picture
        let img = String(data).replace(/\{0\}/g, "3");
        this.picture = String(data2).replace(/\{0\}/g, "3");
        return img;
      }
    },
  },
  activated() {
    this.SerialID = this.$route.query.SerialID;
    this.getHeaderTitle(this.SerialID);
  },
  methods: {
    async getHeaderTitle(id) {
      let res = await api.getHerdTitle(id);
      this.title = res[0];
      // console.log(this.title)
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    Collection(){  //收藏功能
        let carInfo = {SerialID:this.title.SerialID,pinpai:this.title.BrandName,name:this.title.AliasName,price:this.title.DealerPrice,image:this.picture}
        // console.log(carInfo)
        let carList = JSON.parse(localStorage.getItem("car")) || []
        let bool = carList.some((item)=>{
          if(item.SerialID===carInfo.SerialID){
            this.$toast("已经收藏过了")
            return true
          }
        })
        if(!bool){
          carList.push(carInfo)
          localStorage.setItem("car",JSON.stringify(carList))
          this.$toast("收藏成功")
        }
    }
  },
  components: {
    cartab
  }
};
</script>
<style lang="scss">
.carInfo {
  padding: 50px 0 50px;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    color: #fff;
    .van-nav-bar__left {
      i {
        color: #fff !important;
      }
    }
    .van-nav-bar__text,
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .titlePrice {
    display: flex;
    justify-content: space-between;
    padding: 10px 30px 0 20px;
    .price {
      font-size: 18px;
      color: #ff4d4e;
    }
    .del {
      font-size: 12px;
      color: #999;
      margin-top: 3px;
    }
    .youhao {
      margin-top: 2px;
      text-align: center;
      font-size: 12px;
      color: #353535;
    }
  }
  .centerButton {
    display: flex;
    justify-content: center;
    margin: 25px 0;
    button {
      width: 175px;
      height: 35px;
      line-height: 35px;
      border-color: #5f93ff !important;
      color: #5f93ff !important;
    }
    .daikuan {
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px;
    }
    .xundijia {
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
      background: #5f93ff !important;
      color: #fff !important;
    }
  }
  .start {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    text-align: center;
    width: 100%;
    background: #fff;
    height: 63px;
    box-sizing: border-box;
    padding: 10px;
    button {
      width: 170px;
      height: 44px;
    }
    div {
      margin-top: 5px;
      p {
        font-size: 12px;
      }
    }
  }
}
</style>